.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.board-row {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.board-row > button {
  height: 100px;
  width: 100%; 
  margin: 1px;
  font-size: 28px;
  background-color: rgb(199, 199, 199);
  color: rgb(45, 45, 45);
  border: none;
  border-radius: 3px;
}

.board-row > button:hover {
  background: rgb(156, 228, 156);
}

.counter{
  margin-top: 20px;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

hr {
  border: none;
  width: 80%;
  height: 1px;
  background: #000;
}

.counter > button {
  padding: 15px 35px;
  font-size: 18px;
  border: 1px solid rgb(199, 199, 199);
  border-radius: 5px;
  background: rgb(199, 199, 199);
}

.counter > button:active {
  background: plum;
  color: aliceblue;
}